@-webkit-keyframes fadeIn {
0% {
    opacity: 0;
}
20% {
    opacity: 1;
}
100% {
    opacity: 1;
}
}
@keyframes fadeIn {
0% {
    opacity: 0;
}
20% {
    opacity: 1;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes fadeInOut {
0% {
    opacity: 0;
}
10% {
    opacity: 1;
}
90% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
@keyframes fadeInOut {
0% {
    opacity: 0;
}
10% {
    opacity: 1;
}
90% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
